.services {
  background: linear-gradient(180deg, #0c0c1d, #111132);
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  @media (max-width: 768px) {
    /* width: 768px; */
    animation: none;
    overflow: hidden;
    /* gap: none; */
  }
}
.services .textContainer {
  flex: 1;
  align-self: flex-end;
  text-align: right;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 20px;
  @media (max-width: 768px) {
    align-self: center;
    flex-direction: column;
    justify-content: center;
    padding-left: 100px;
  }
}
.textContainer p {
  font-weight: 200;
  font-size: 20px;
  color: grey;
  text-align: right;
  @media (max-width: 768px) {
    text-align: right;
    font-size: 15px;
  }
}
hr {
  width: 500px;
  border: none;
  border-top: 0.5px solid grey;
}
.titleContainer {
  flex: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  @media (max-width: 768px) {
    flex: 1;
    width: 100%;
    /* padding-bottom: 20px; */
  }
}
.title {
  display: flex;
  gap: 50px;
  @media (max-width: 768px) {
    flex-direction: column;
    text-align: center;
    align-items: center;
    gap: 10px;
  }
}
.titleContainer h1 {
  font-size: 90px;
  font-weight: 100;
  @media (max-width: 768px) {
    font-size: 30px;
    line-height: 55px;
  }
}
.titleContainer img {
  width: 300px;
  height: 100px;
  border-radius: 50px;
  object-fit: cover;
  @media (max-width: 768px) {
    width: 200px;
    height: 50px;
  }
}
.listContainer {
  flex: 2;
  display: flex;
  max-width: 1366px;
  margin: auto;
  @media (max-width: 768px) {
    /* width: 100%; */
    flex-direction: column;
    overflow: scroll;
    width: 768px;
  }
}
.box {
  border: 1px solid grey;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px;
  @media (max-width: 768px) {
    align-items: center;
    /* align-self: center; */
    /* align-content: flex-start; */
    /* justify-self: center; */
    width: fit-content;
    border: none;
    align-items: center;
    /* padding-left: 20%; */
    margin-left: 17%;
    gap: 10px;
    /* background-color: aqua; */
  }
}
.box button {
  background-color: orange;
  padding: 10px;
  width: 300px;
  border: none;
  cursor: pointer;
  font-size: 24px;
  color: white;
  @media (max-width: 768px) {
    background-color: transparent;
    border: 1px solid orange;
    color: orange;
    width: 100%;
    border-radius: 5px;
    padding: 5px;
  }
}
.title button {
  width: 300px;
  height: 100px;
  background-color: orange;
  color: white;
  border: none;
  border-radius: 50px;
  font-size: 24px;
  cursor: pointer;
  @media (max-width: 768px) {
    font-size: 16px;
    width: 200px;
    height: 80px;
  }
}
.box p {
  @media (max-width: 768px) {
    display: none;
  }
}
