* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "DM Sans", sans-serif;
  color: lightgray;
}

body {
  background-color: #0c0c1d;
}

html {
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
}
